import React, { memo, useContext } from 'react';
import { WebchatContext } from '@botonic/react';
import TextLink from '../../../TextLink';
import styles from './WelcomeMessage.module.css';

interface Props {
  title: string;
  subTitle: string;
}

export const WelcomeMessage = memo((props: Props) => {
  const {
    webchatState: { theme },
  } = useContext(WebchatContext);
  const { title, subTitle } = props;

  if (!title && !subTitle) {
    return null;
  }

  return (
    <div
      className={styles.container}
      style={{ backgroundColor: theme?.initialScreen?.welcomeMessageBackgroundColor }}
    >
      <h2
        className={styles.title}
        style={{
          fontSize: theme?.initialScreen?.welcomeMessageTitleFontSize,
          fontFamily: theme?.initialScreen?.welcomeMessageTitleFontFamily,
          color: theme?.initialScreen?.welcomeMessageTitleColor,
        }}
      >
        <TextLink target={title} />
      </h2>
      <p
        className={styles.subTitle}
        style={{
          fontSize: theme?.initialScreen?.welcomeMessageContentFontSize,
          fontFamily: theme?.initialScreen?.welcomeMessageContentFontFamily,
          color: theme?.initialScreen?.welcomeMessageContentColor,
        }}
      >
        <TextLink target={subTitle} />
      </p>
    </div>
  );
});
